













import { Component, Vue, Provide } from 'vue-property-decorator';
import NavBar from './Nav.vue';
import Footer from './Footer.vue';
import DataList from './DataList.vue';
import About from '@/views/About.vue';

interface event {
    head: string;
    content: string;
}

@Component({
    components: {
        NavBar,
        Footer,
        DataList,
        About,
    },
})
export default class List extends Vue {
    @Provide() public tab: number = 1;
    @Provide() public showabout: boolean = false;
    @Provide() public stage1: event[] = [
        {
            head: 'test',
            content: 'this is a test sample',
        },
        {
            head: 'test2',
            content: 'test2',
        },
    ];
    @Provide() public stage2: event[] = [
        {
            head: 'test2',
            content: 'this is a test sample',
        },
        {
            head: 'test2',
            content: 'test2',
        },
    ];
    @Provide() public stage3: event[] = [
        {
            head: 'test3',
            content: 'this is a test sample',
        },
        {
            head: 'test2',
            content: 'test2',
        },
    ];

    public changeTab(tab: number) {
        if (tab < 4) {
            this.tab = tab;
            this.showabout = false;
        } else {
            this.showabout = true;
        }
    }

    public openLogin() {
        this.$emit('openlogin');
    }

    get renderData() {
        if (this.tab === 1) {
            return this.stage1;
        } else if (this.tab === 2) {
            return this.stage2;
        } else {
            return this.stage3;
        }
    }
}
